@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,700,800&display=swap");

.body {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

::-webkit-scrollbar {
  width: 0px;
}

.font {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

.fontBold {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

.white {
  color: white;
}

.black {
  color: #2b2b2b;
}

.shown {
  position: fixed;
  bottom: 0;
  z-index: 20;
  display: block;
  opacity: 1;
  transition: opacity 0.5s ease-out;
}


.hidden {
    position: fixed;
    bottom: 0;
    z-index: 20;
    display: none;
    opacity: 0;
    transition: opacity 1s ease-out;
  }
  
.portfolioShow{
  display: flex;
  opacity: 1;
  animation: show 0.5s ease-in;
} 
.portfolioHidden{
  display: none;
  opacity: 0;
  animation: visible 0.5s ease-in;
}


@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
 }

@keyframes hide {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
 }