@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

::-webkit-scrollbar {
  width: 0px;
}

.font {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

.fontBold {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

.white {
  color: white;
}

.black {
  color: #2b2b2b;
}

.shown {
  position: fixed;
  bottom: 0;
  z-index: 20;
  display: block;
  opacity: 1;
  transition: opacity 0.5s ease-out;
}


.hidden {
    position: fixed;
    bottom: 0;
    z-index: 20;
    display: none;
    opacity: 0;
    transition: opacity 1s ease-out;
  }
  
.portfolioShow{
  display: flex;
  opacity: 1;
  -webkit-animation: show 0.5s ease-in;
          animation: show 0.5s ease-in;
} 
.portfolioHidden{
  display: none;
  opacity: 0;
  -webkit-animation: visible 0.5s ease-in;
          animation: visible 0.5s ease-in;
}


@-webkit-keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
 }


@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
 }

@-webkit-keyframes hide {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
 }

@keyframes hide {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
 }
